import React, {useRef, useEffect} from 'react'
import LinksDropdown from "../Dropdown/selected.jsx"


export default function Goods( props ) {

    const {
        workshop,
        quantity,
        id,
        addGoodsCount = Function.prototype,
        removeGoodsCount = Function.prototype
    } = props

    const countRef = useRef()

    useEffect( () => {
        countRef.current.value = +quantity.toFixed(1) 
    }, [ quantity ] )


    return (
        <>
            <div className="order__info__item">
                <LinksDropdown
                    key={"links" + id}
                    data={workshop} />
            <div className="info--count">
                    <div className="order__info__item__count">
                        <input
                            // onChange={()=> GoodsCount(id, countRef.current.value)}
                            ref={countRef}
                            type="text"
                            className="input"
                            placeholder="1"
                            name="CountGoodsOrder"
                            // value={quantity}
                            onBlur={() => {addGoodsCount( id, +countRef.current.value); console.log( countRef.current.value )}}
                        />
                    </div>
                        <div style={{position: "relative", display: 'block'}}>
                        <div
                            onClick={() => addGoodsCount( id )}
                            className="item__add">
                            &#8249;
                        </div>
                        <div
                            className="item__remove"
                            onClick={() => removeGoodsCount( id, quantity )}>
                            &#8249;
                        </div>
                    </div>
                </div>
            </div>
            <div className="order__info_comment">
                <label className="label" htmlFor="inputKomProduct">
                    Комментарий к товару
                </label>
                <div>
                    <input
                        name="inputKomProduct"
                        type="text"
                        className="input"
                        placeholder="..." />
                </div>
            </div>
        </>
    )
}