import React, {useState, useEffect, useRef} from 'react'
import "./_selected.scss"

export default function LinksDropdown( props ) {

    const {
        data = [],
        now,
        widths,
        monthSelect = Function.prototype
    } = props

    const [ Win_width, setWin_width ] = useState( document.documentElement.clientWidth )
    window.addEventListener( "resize", () => setWin_width( document.documentElement.clientWidth ) )

    const Select = useRef()
    const SelectText = useRef()
    const [ isOpenSelect, setIsOpenSelect ] = useState( false )



    useEffect( () => {
        SelectText.current.innerText = Select.current.options[ Select.current.value ].text
    }, [ isOpenSelect, now ] )

    return (
        <div
            tabIndex="-1"
            key={now}
            onBlur={( e ) => {if( Win_width > 540 ) setIsOpenSelect( false )}}
            style={{width: widths ? ( {widths, marginLeft: "0"} ) : "15rem"}}
            onClick={( e ) => {
                if( Win_width > 540 ) setIsOpenSelect( !isOpenSelect ); if( Win_width <= 540 ) {
                    Select.current.focus(); Select.current.size = Select.current.lenght
                }
            }}
            className={isOpenSelect ? " selectric-wrapper selectric-js-select-time selectric-belowselectric-open selectric-focus " : "selectric-wrapper selectric-js-select-time selectric-below"} >
            <div className={Win_width <= 540 ? "selectric-is-native selectric-hide-select" : "selectric-hide-select"} >
                <select
                    tabIndex="0"
                    ref={Select}
                    onChange={() => {
                        SelectText.current.innerText = Select.current.options[ Select.current.value ].text
                    }}
                    className="js-select-time"
                    name="ShippingCalculateForm[data]"
                >
                    <option key={"op"} value="0" >{now ? now : 'Выбрать'}</option>
                    {data.map( ( item, index ) => ( <>
                        <option
                            key={"opt" + index}
                            value={item.id}>{item.data}</option>
                    </> ) )}
                </select>
            </div>
            <div
                className="selectric">
                <span ref={SelectText} className="label" ></span>
                <b className="button">▾</b >
            </div>

            {isOpenSelect && <div
                className="selectric-items"
                style={{display: "block", zIndex: "29", width: "100%"}} >
                <div className="selectric-scroll">
                    <ul>
                        <li
                            key={"l"}
                            className="highlighted"
                            data-index="0"
                            onClick={( e ) => {
                                setIsOpenSelect( !isOpenSelect )
                                Select.current.options[ e.target.dataset.index ].selected = true
                            }}
                        >
                            {now ? now : 'Выбрать'}</li >
                        {data.map( ( item, index ) => ( <>
                            < li
                                key={"li" + index}
                                data-index={item.id}
                                onClick={( e ) => {
                                    setIsOpenSelect( !isOpenSelect )
                                    Select.current.options[ e.target.dataset.index ].selected = true
                                    monthSelect( e.target.dataset.index )
                                }}>{item.data}</li>
                        </> ) )}
                    </ul>
                </div>
            </div>
            }
            <input className="selectric-input" tabIndex="1" />
        </div>
    )
}